<template>
    <div v-if="ready" style='height:87.5vh'>
        <div class="col-12">
            <div id='top_page' class="form-group">
                <!--live search-->           
                    <div class='col-12 flex px0'>
                        <div class="col-3 px0">
                            <div class="col-12 px0">
                            <label>Select Department</label>
                            <select v-model="dept" class="form-control" v-if="dept_ready" @change="fetch_info()">
                                <option v-for="dep in dept_list " :value="dep.deptmentid">{{ dep.department }}</option>
                                <option v-if="checkCCA==false" value='CCA'>Co-curricular Activity Committee</option>
                            </select>
                            </div>
                            <div class="col-12 flex px0" style='margin-top:0.4em'>
                                <div class="col-6 pl0">
                                    <button type="button" class="form-control" @click="newplan">New</button>  
                                </div>
                                <!--<button type="button" class="form-control" style="margin-top:0.4em;width:32.5%">Import</button>-->
                                <div class="col-6 pr0">
                                    <select v-model="stat" class="form-control select" v-if="dept_ready" @change="fetch_info()">
                                        <option value='*'>All Records</option>
                                        <option value='active'>Active Records</option>
                                        <option value='rejected'>Rejected Records</option>
                                        <option value='cancelled'>Cancelled Records</option>
                                    </select>
                                </div>
                            </div>                                                          
                        </div>
                        <div class="col-6"></div>
                        <div class="col-3 px0">
                            <div class="col-12 px0">
                                <label>Search Record</label>
                                <input type='text' class="form-control" placeholder="Search Record" v-model="key" @keyup="fetch_info()">
                            </div>
                            <div class="col-12 flex px0" style="margin-top:0.4em">
                                <div class="col-6 flex pl0">
                                    <input type="number" v-model="st" class="form-control" @change="update_period()"/>
                                </div>
                                <div class="col-6 flex pr0">
                                    <input type="number" v-model="ed" class="form-control" readonly/>
                                </div>
                            </div>   
                        </div>
                    </div>
            </div>  
            <table class="table table-striped first-td-padding">
                <thead style="background-color:rgb(238,28,37)">
                    <tr>
                        <th style='vertical-align:top'>Action</th>
                        <th style='width:5%!important;vertical-align:top'>ID</th>
                        <th style='vertical-align:top'>Date</th>
                        <th style='vertical-align:top'>Procurement ID</th>
                        <th style='vertical-align:top'>Recipient</th>
                        <th style='vertical-align:top'>Item Description</th>
                        <th style='vertical-align:top'>Fund Source</th>
                        <th style='vertical-align:top'>Amount HK$</th>
                        <th style='vertical-align:top'>Status</th>
                        <th style='vertical-align:top'>Form</th>
                        <th style='vertical-align:top'>Related File(s)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="l in data.length" style='font-size:1rem!important'>
                        <td style='font-size:1rem!important'>
                            <div>
                                <u v-if="data[l-1].editable" @click="edit_reimb_record(data[l-1].tag)">Edit</u>
                            </div>
                            <div>
                                <u v-if="data[l-1].editable" @click="cancel_reimb_record(data[l-1].tag)">Cancel</u>
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>{{ data[l-1].tag.toUpperCase() }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].date_app }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].proc_id }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].recipient }}</td>
                        <td style='font-size:1rem!important'>    
                            <div v-for="c in data[l-1].item.length" class='inner_row'>
                                {{ data[l-1].item[c-1] }}
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>
                            <div v-for="f in data[l-1].fundsrc.length" class='inner_row'>
                                {{ data[l-1].fundsrc[f-1] }}
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>
                            <div v-for="a in data[l-1].amount.length" class='inner_row'>
                                {{ data[l-1].amount[a-1] }}
                            </div>
                        </td>
                        <td :class="data[l-1].approve_status">{{ data[l-1].approve_status }}</td>
                        <td style='font-size:1rem!important'>
                            <u v-if="(data[l-1].system_stat=='enable')&&(!data[l-1].isHead)" @click="view_form(data[l-1].tag)">View</u>
                            <u v-if="(data[l-1].system_stat=='enable')&&(data[l-1].isHead)" @click="view_form(data[l-1].tag)">Endorse</u>
                            <u v-if="data[l-1].system_stat=='req'">To Be Processed</u>
                        </td>
                        <td style='font-size:1rem!important'>
                            <div v-for="p in data[l-1].pdf.length">
                                <a :href="data[l-1].pdf[p-1]" target="_blank">&bull;<u>{{ data[l-1].pdf_file_name[p-1] }}</u></a>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>       
        </div>
        <div class="corner_nav">
            <div class="col-12">
                <div class="corner_item" @click="scrollTop">
                    <i class="fa fa-2x fa-arrow-up" style='color:rgba(255,255,255,0.9)'></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            checkCCA:false,
            indept:false,
            user_check:false,
            dept_ready:false,
            dept_list:[],
            ready:false,
            data:[],
            dept:'',
            key:'',
            stat:'active',
            st:'',
            ed:'',
            
        }
    },
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<2){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
        await axios.get('Procurement/department/backend/dept_member.php')
            .then(
                (response)=>{
                    this.dept_list = response.data;
                    this.dept = this.dept_list[0].deptmentid;
                    this.dept_ready = true;
                    var i=0;
                    for(i=0;i<this.dept_list.length;i++){
                        if(this.dept_list[i].deptmentid=='CCA'){
                            this.checkCCA = true;
                        }
                    }               
                }
            );
        await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )    
        var query = document.getElementById('tmp_id').value;
        if((query!='')&&(query!=null)&&(query!=undefined)){
            var dpt = document.getElementById('tmp_id_2').value;
            if((dpt!='')&&(dpt!=null)&&(dpt!=undefined)){
                this.dept = document.getElementById('tmp_id_2').value;
                this.key = query;        
            }
        }  
        document.getElementById('tmp_id_2').value = '';
        document.getElementById('tmp_id').value = '';
        await this.fetch_info();
        this.ready = true;
    },
    mounted(){
    window.addEventListener('focus', this.fetch_info);
    },
    beforeDestroy() {
    window.removeEventListener('focus', this.fetch_info);
    },
    methods:{
        scrollTop(){
            document.getElementById('top_page').scrollIntoView();
        },
        async update_period(){
            this.ed = parseInt(this.st)+1;
            this.$forceUpdate();
            await this.fetch_info();
        },
        cancel_reimb_record(id){
            document.getElementById('tmp_id').value=id
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('tag',id);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            this.$confirm('Confirm Cancel Record : '+id+'?')
                .then(
                    ()=>{
                        axios.post('Procurement/reimbursement/backend/cancelreimb_v2.php',formData)
                            .then(
                                (response)=>{
                                    if(response.data=="OK"){
                                        this.$alert("Successfully Cancelled Record :"+id)
                                            .then(
                                                ()=>{
                                                    this.fetch_info();
                                                }
                                            )
                                        //location.reload();
                                    } else {
                                        this.$alert(response.data);
                                    }
                                }
                            ) 
                    }
                )
        },
        newplan(){
            this.$router.push('reimbursement_form');
        },
        view_form(tag){
            document.getElementById('tmp_id').value = tag;
            document.getElementById('st_tmp').value = this.st+'-08';
            document.getElementById('ed_tmp').value = this.ed+'-07';
            document.getElementById('doc_type').value = 'Reimbursement';
            this.$router.push('../wrapper/form_view');
        },
        edit_reimb_record(tag){
            document.getElementById('tmp_id').value = tag;
            document.getElementById('st_tmp').value=this.st+'-08';
            document.getElementById('ed_tmp').value=this.ed+'-07';
            this.$router.push('edit_reimb_record');
        },
        async fetch_info(){
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('stat',this.stat);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('key',this.key);
            await axios.post('Procurement/reimbursement/backend/reimb_json.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data = [];
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .flex{
        display:flex;
        flex-direction: row;
    }
    .px0{
        padding-left:0;
        padding-right: 0;
    }
    .pl0{
        padding-left:0;
    }
    .pr0{
        padding-right:0;
    }
    th,td{
        font-size:14px!important;   
        border:1px solid white;
    }
    td{
        font-size:1rem!important;
    }
    td,th{
        vertical-align:top;
    }
    .approved{
        color:#1bd615;
        text-transform: capitalize;
        font-size:1rem;
    }
    .pending{
        text-transform: capitalize;
        font-size:1rem;
    }
    .cancelled, .rejected{
        color:red;
        text-transform: capitalize;
        font-size:1rem;
    }
    u,a{
        cursor:pointer;
    }
</style>


